html {
  background: #ffffff;
  margin: 0;
}

body {
  min-height: 400px;
  min-width: 600px;
}

.outer-wrapper .fas {
  margin-right: 10px;
}
.outer-wrapper h4 {
  margin-top: 5px;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #cccccc;
}
.outer-wrapper .settings-wrap {
  line-height: 1;
  margin-bottom: 15px;
}
.outer-wrapper .settings-wrap .font-picker-container #font-label {
  font-weight: 600;
}
.outer-wrapper .settings-wrap .enable-regexp-container > div {
  padding-top: 10px;
}
.outer-wrapper .settings-wrap .enable-regexp-container #enable-regexp-label {
  font-weight: 600;
}
.outer-wrapper .settings-wrap .enable-regexp-container #enable-regexp-help-block {
  display: inline-block;
  margin: 0;
  position: relative;
  top: 2px;
}
.outer-wrapper .export-import-wrap {
  margin-top: 25px;
}
.outer-wrapper .export-import-wrap .export-import-container .import-file-label {
  cursor: pointer;
  padding-right: 10px;
  position: relative;
  top: 2px;
}